import { getPickersLocalization } from './utils/getPickersLocalization';
const views = {
  hours: '',
  minutes: '',
  seconds: ''
}; // This object is not Partial<PickersLocaleText> because it is the default values

const zhCNPickers = {
  // Calendar navigation
  previousMonth: '',
  nextMonth: '',
  // View navigation
  openPreviousView: '',
  openNextView: '',
  calendarViewSwitchingButtonAriaLabel: view => view === 'year' ? '' : '',
  // DateRange placeholders
  start: '',
  end: '',
  // Action bar
  cancelButtonLabel: '',
  clearButtonLabel: '',
  okButtonLabel: '',
  todayButtonLabel: '',
  // Clock labels
  clockLabelText: (view, time, adapter) => `Select ${views[view]}. ${time === null ? '' : `${adapter.format(time, 'fullTime')}`}`,
  hoursClockNumberText: hours => `${hours}`,
  minutesClockNumberText: minutes => `${minutes}`,
  secondsClockNumberText: seconds => `${seconds}`,
  // Open picker labels
  openDatePickerDialogue: (rawValue, utils) => rawValue && utils.isValid(utils.date(rawValue)) ? `${utils.format(utils.date(rawValue), 'fullDate')}` : '',
  openTimePickerDialogue: (rawValue, utils) => rawValue && utils.isValid(utils.date(rawValue)) ? `${utils.format(utils.date(rawValue), 'fullTime')}` : '',
  // Table labels
  timeTableLabel: '',
  dateTableLabel: ''
};
export const zhCN = getPickersLocalization(zhCNPickers);